.Header {
    height: 350px;
    background: linear-gradient(180deg, rgba(58, 51, 48, 0) 0%, rgba(58, 51, 48, 0.31558) 0.01%, rgba(58, 51, 48, 0.581335) 0.02%, rgba(58, 51, 48, 0.68) 45.86%, rgba(58, 51, 48, 0.68) 64.09%), url("../../img/book.jpeg") center center/cover no-repeat;
    display: flex;
    justify-content: center;
    align-items: center;
}

.Header h1 {
    margin: 0;
    color: white;
}

.Header form {
    margin-top: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 400px;
    background-color: white;
    padding: 5px;
}

.Header form input {
    border: none;
    outline: none;
}

.Header form button {
    border: none;
    background-color: white;
    cursor: pointer;
}

.Selector {
    margin-top: 10px;
    padding: 10px;
    color: white;
    font-size: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.Selector select {
    height: 25px;
}

.add-sort-button {
    width: 150px;
    height: 30px;
    background-color: white;
    border: none;
    cursor: pointer;
    border-radius: 10px;
    font-size: 15px;
}

.add-sort-button:hover {
    box-shadow: 0 4px 15px 0 rgba(0, 0, 0, .25);
}

@media (max-width: 450px) {
    .Header form {
        width: 100%;
    }

    .Selector {
        flex-direction: column;
    }

    .Header {
        height: 250px;
    }

    .Header form {
        margin-top: 15px;
    }
    .Selector {
        margin-top: 7px;
        padding: 7px;
        font-size: 12px;
    }

}